import React, {Component} from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'
import '../../../src/css/components/commonPages/Header.css'

const MenuItem = ({d}) => (
    <li>
      <Link to={d.link} target={d.target}>
            {d.menu}
      </Link>
    </li>
)

export default class Header extends Component {

    constructor(props) {
        super(props);
        this.state = {logo:{link:"", title:""}, menu:[], right:[]};
    }

    componentDidMount() {
      axios
      .get('https://s3.ap-northeast-2.amazonaws.com/hitthebuk.github.io/menu.json')
      .then((res)=>{
        this.setState(
          {
            logo : res.data.logo,
            menu : res.data.menu,
            right : res.data.right
          }
        );
      });
  	}

    render() {

      const {title, link} = this.state.logo;

      return (
          <nav className="navbar navbar-default navbar-fixed-top">
            <div className="container">
              <div id="navHeader" className="navbar-header">
                <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                  <span className="sr-only">Toggle navigation</span>
                  <span className="icon-bar"></span>
                  <span className="icon-bar"></span>
                  <span className="icon-bar"></span>
                </button>
                <Link to={link} className="navbar-brand">{title}</Link>
              </div>
              <div id="navbar" className="navbar-collapse collapse">
                <ul className="nav navbar-nav">
                  {this.state.menu.map((d,i)=>(<MenuItem d={d} key={i}></MenuItem>))}
                </ul>
                <ul className="nav navbar-nav navbar-right">
                  {this.state.right.map((d,i)=>(<MenuItem d={d} key={i}></MenuItem>))}
                </ul>
              </div>
            </div>
          </nav>
      );
    }
};



