import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import '../../../src/css/pages/GuideDocList.css';

export default class GuideDocList extends Component {
    constructor(props) {
        super(props);
        this.state = {contents:[]};
        this.url = makeUrl(this.props.match.params.category);
    }
    render() {
        let totalCnt = this.state.contents.length>0?this.state.contents.length:1;
        let pg = this.props.match.params.pg?this.props.match.params.pg:1;
        return (
            <div id="contents" className="container" role="main">
              <div id="categoryTitle" className="page-header">
                <h1>{this.props.match.params.category} <small>({this.state.contents.length})</small></h1>
              </div>
              {this.state.contents.map((d,i)=>{ return (<ContentListBox d={d} pg={this.props.match.params.pg} key={i} />); })}
              <Paging totalCnt={totalCnt} dataSize={10} pageSize={5} pageNo={pg} />
            </div>
        );
    }
    componentDidUpdate(nextProps, nextState) {
      if(nextProps.match.params.category !== this.props.match.params.category) {
        this.url = makeUrl(this.props.match.params.category);
        clearInterval(this.getList);
        timer(this, this.url);
        this.getList = setInterval(()=>{timer(this, this.url)},30000);
      }
    }
    componentDidMount() {
      timer(this, this.url);
      this.getList = setInterval(()=>{timer(this, this.url)},30000);
    }
    componentWillUnmount() {
      clearInterval(this.getList);
    }
}

const Paging = (d) => {
  let totalCnt = parseInt(d.totalCnt, 10); // 전체레코드수
  let dataSize = parseInt(d.dataSize, 10); // 페이지당 보여줄 데이타수
  let pageSize = parseInt(d.pageSize, 10); // 페이지 그룹 범위 1 2 3 5 6 7 8 9 10
  let pageNo = parseInt(d.pageNo, 10); // 현재페이지

  if(totalCnt === 0){ return ""; }

  // 페이지 카운트
  let pageCnt = totalCnt % dataSize;
  if(pageCnt === 0){
    pageCnt = parseInt(totalCnt / dataSize, 10);
  } else {
    pageCnt = parseInt(totalCnt / dataSize, 10) + 1;
  }

  let pRCnt = parseInt(pageNo / pageSize, 10);
  if(pageNo % pageSize === 0) { pRCnt = parseInt(pageNo / pageSize, 10) - 1; }

  //이전 화살표
  let prevBtLink;
  if(pageNo > pageSize) {
    if(pageNo % pageSize === 0) { prevBtLink = pageNo - pageSize; }
    else { prevBtLink = pageNo - pageNo % pageSize; }
    prevBtLink = "/doc/nodejs/"+prevBtLink;
  }

  //paging Bar
  let pageArray = [];
  for(var index=pRCnt * pageSize + 1;index<(pRCnt + 1)*pageSize + 1;index++) {
    pageArray.push({flag:(index===pageNo), index:index});
    if(index === pageCnt) { break; }
  }

  //다음 화살표
  let nextBtLink;
  if(pageCnt > (pRCnt + 1) * pageSize){
    nextBtLink = "/doc/nodejs/"+((pRCnt + 1)*pageSize+1);
  }

  return (
    <nav className="text-center">
      <ul className="pagination">
        <MakePrevBt prevBtLink={prevBtLink} />
        {pageArray.map((d,i)=>{ return (<MakePageBt d={d} key={i} />); })}
        <MakeNextBt nextBtLink={nextBtLink} />
      </ul>
    </nav>
  );

}

const MakePrevBt = ({prevBtLink}) => {
  if(prevBtLink) return (<li><Link to={prevBtLink} aria-label="Previous"><span aria-hidden="true">&laquo;</span></Link></li>);
  else return (<li className="disabled"><Link to="#" aria-label="Previous"><span aria-hidden="true">&laquo;</span></Link></li>);
}

const MakePageBt = ({d}) => {
  if(d.flag) return (<li className="active"><Link to="#">{d.index} <span className="sr-only">(current)</span></Link></li>);
  else return (<li><Link to={"/doc/nodejs/"+d.index}>{d.index}</Link></li>);
}

const MakeNextBt = ({nextBtLink}) => {
  console.log("nextBtLink", nextBtLink);
  if(nextBtLink) return (<li><Link to={nextBtLink} aria-label="Next"><span aria-hidden="true">&raquo;</span></Link></li>);
  else return (<li className="disabled"><Link to="#" aria-label="Next"><span aria-hidden="true">&raquo;</span></Link></li>);
}

const makeUrl = (category) => {
  return "https://s3.ap-northeast-2.amazonaws.com/hitthebuk.github.io/guidedoc/"+category+".json";
}

const timer = (thisis, url) => {
  axios
  .get(url)
  .then((res) => {
    thisis.setState({
      contents : res.data.contents
    });
  });
}

const ContentListBox = ({d, pg}) => {
  let link = "/doc/"+d.category+"/"+pg+"/"+d.idx;
  return (
    <div className="media" style={{padding:"20px 0 15px 0", minHeight:100, maxWidth:820, margin:"auto", borderBottom:"1px solid #ebebeb"}}>
      <MakeTextTag link={link} title={d.title} category={d.category} desc={d.desc} date={d.updDt?d.updDt:d.regDt} />
      <MakeImgTag link={link} imgPath={d.imgPath} />
    </div>
  );
}

const MakeTextTag = (d) => {
  return (
    <div className="media-body">
      <h3 className="media-heading"><Link to={d.link} style={{color:'#333'}}>{d.title}</Link></h3>
      <p><Link to={d.link} style={{color:'#6b6b6b'}}>{d.desc}</Link></p>
      <p><Link to={"/doc/"+d.category} className="categoryLink">{d.category}</Link> <span className="txt_bar"></span> <span className="txt_style">{d.date}</span></p>
    </div>
  );
}

const MakeImgTag = (d) => {
  if(d.imgPath) {
    return (
      <div className="media-right">
        <Link to={d.link}>
          <img className="media-object" src={d.imgPath} alt="이미지" />
        </Link>
      </div>
    );
  } else {
    return (<div/>);
  }
}



