import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import '../../../src/css/pages/Peoples.css';

export default class Peoples extends Component {
    constructor(props) {
      super(props);
      this.state = {peoples:[]};
    }
    render() {
        return (
          <div id="contents" className="container" role="main">
            {this.state.peoples.map((d,i)=>{return (<MakeNameCard d={d} key={i} />);})}
          </div>
        );
    }
    componentDidMount() {
      axios
      .get("https://s3.ap-northeast-2.amazonaws.com/hitthebuk.github.io/peoples.json")
      .then((res) => {
          this.setState(
            {
              peoples:res.data.peoples
            }
          );
      });

    }
}

const MakeNameCard = ({d}) => {
  return (
    <div className="col-md-6 col-sm-6 col-xs-12 profile_details">
      <div className="well profile_view">
        <div className="col-sm-12">
          <h4 className="brief"><i>{d.inCharge}</i></h4>
          <div className="left col-xs-7">
            <h2>{d.id}</h2>
            <p><strong>About: </strong> {d.about} </p>
            <ul className="list-unstyled">
              <li><span className="glyphicon glyphicon-envelope" aria-hidden="true"></span> Email: {d.email}</li>
              <li><span className="glyphicon glyphicon-earphone" aria-hidden="true"></span> Phone: {d.phone}</li>
            </ul>
          </div>
          <div className="right col-xs-5 text-center">
            <img src={d.imgPath} alt="" className="img-circle img-responsive" />
          </div>
        </div>
        <div className="col-xs-12 bottom text-center">
          <div className="col-xs-12 col-sm-8 emphasis">
            <MakePoint point={d.point} />
          </div>
          <div className="col-xs-12 col-sm-4 emphasis">
            <button type="button" className="btn btn-primary btn-xs">
              <span className="glyphicon glyphicon-user" aria-hidden="true"></span> View Profile
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

const MakePoint = ({point}) => {
  let array = [point,point,point,point,point];
  return (
    <p className="ratings">
      <a>{point}.0</a>&nbsp;
      {array.map((d,i)=>{return(<MakeStar flag={d>i} key={i} />);})}
    </p>
  );
}

const MakeStar = ({flag}) => {
  if(flag) return (<Link to="/"><span className="glyphicon glyphicon-star" aria-hidden="true"></span></Link>);
  else return (<Link to="/"><span className="glyphicon glyphicon-star-empty" aria-hidden="true"></span></Link>);
}



