import React from 'react';

export default class AddDisqus extends React.Component {

    render() {
        return (
          <div>
            <div id="disqus_thread" className="container"></div>
            <div id="reloadjs"></div>
          </div>
        );
    }

    componentWillUpdate() {
      let s = document.createElement("script");
      s.type = "text/javascript";
      s.async = true;
      s.innerHTML = "try{ DISQUS.reset({reload: true}); }";
      s.innerHTML += "catch(e) {";
      s.innerHTML += "  (function(){";
      s.innerHTML += "    var d = document, s = d.createElement('script');";
      s.innerHTML += "    s.src = 'https://hitthebuk.disqus.com/embed.js';";
      s.innerHTML += "    s.setAttribute('data-timestamp', +new Date());";
      s.innerHTML += "    (d.head||d.body).appendChild(s);";
      s.innerHTML += "  })();";
      s.innerHTML += "}";
      document.getElementById("reloadjs").innerHTML = "";
      document.getElementById("reloadjs").appendChild(s);
    }

    componentDidMount() {
        let s = document.createElement("script");
        s.type = "text/javascript";
        s.async = true;
        s.innerHTML = "try{ DISQUS.reset({reload: true}); }";
        s.innerHTML += "catch(e) {";
        s.innerHTML += "  (function(){";
        s.innerHTML += "    var d = document, s = d.createElement('script');";
        s.innerHTML += "    s.src = 'https://hitthebuk.disqus.com/embed.js';";
        s.innerHTML += "    s.setAttribute('data-timestamp', +new Date());";
        s.innerHTML += "    (d.head||d.body).appendChild(s);";
        s.innerHTML += "  })();";
        s.innerHTML += "}";
        document.getElementById("reloadjs").innerHTML = "";
        document.getElementById("reloadjs").appendChild(s);
    }

}



