import React, {Component} from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import {Header, Footer} from './components/commonPages';
import {Home, GuideDoc, GuideDocList, Peoples} from './components/pages';

export default class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <Header/>
          <Switch>
            <Route exact path="/" component={Home}/>
            <Route path="/doc/:category/:pg/:idx" component={GuideDoc}/>
            <Route path="/doc/:category/:pg" component={GuideDocList}/>
            <Route path="/doc/:category" component={GuideDocList}/>
            <Route path="/peoples" component={Peoples}/>
          </Switch>
          <Footer/>
        </div>
      </Router>
    );
  }
}



