import React from 'react';
import {Link} from 'react-router-dom'
import '../../../src/css/components/commonPages/Footer.css';

const Footer = () => {
    return (
      <footer>
        <hr className="featurette-divider" />
        <div className="container">
          <p className="pull-right"><a href="#top">TOP</a></p>
          <p className="pull-right" style={{paddingRight:"20px"}}><Link to="/peoples">내부자들</Link></p>
          <p>&copy; Copyright 2017 hitthebuk.</p>
        </div>
      </footer>
    );
};

export default Footer;



