import React, { Component } from 'react'
import axios from 'axios'

export default class About extends Component {
    constructor(props) {
        super(props);
        this.state = {message:{},contents:[]};
    }
    render() {
        return (
            <div id="contents" className="container" role="main">
              <div className="jumbotron"><h1>{this.state.message.title}</h1><p>{this.state.message.desc}</p></div>
              {this.state.contents.map((d,i)=>{ return (<ContentBox d={d} key={i} />); })}
            </div>
        );
    }
    componentDidMount() {
      axios
      .get('https://s3.ap-northeast-2.amazonaws.com/hitthebuk.github.io/home.json')
      .then((res)=>{
        this.setState(
          {
            message : res.data.message,
            contents : res.data.contents
          }
        );
      });
  	}
}

const ContentBox = ({d}) => (
    <div><div className="page-header"><h3 dangerouslySetInnerHTML={{__html:d.title}} /></div><div dangerouslySetInnerHTML={{__html:d.content}} /></div>
)



